import React from 'react';
import styled from "styled-components";

import MapPoint from '../../../img/map-point.svg';

export const Text = (props) => {
    const Text = styled.p`
        line-height: 1;
        font-weight: ${props.weight};
        margin-bottom: ${props.margin};
        font-size: ${props.size};
        color: ${props.color};
    `;
    return <Text>{props.text}</Text>
};

const Container = styled.div`
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    
    height: 100%;
    padding: 32px 64px 32px 64px;
    
    box-sizing: border-box;
`;

const Header = styled.div`
    display: flex;
    justify-content: space-between;
`;

const Main = styled.div``;

const Footer = styled.div`
    display: flex;
    justify-content: space-between;
`;

const HeaderPlace = styled.div`
    cursor: pointer;
   
    display: flex;
    align-items: center;
`;
const HeaderImg = styled.img`
    margin-right: 8px;
`;

const MainContainer = styled.div`
    margin-bottom: 60px;
`;

const Button = styled.button`
    cursor: pointer;
    line-height: 1;
    font-weight: 500;
    font-size: 18px;
    color: #FFFFFF;
    
    padding: 15px 60px 15px 60px;
    
    background: linear-gradient(90deg, #0EC261 2.61%, #039F67 112.6%);
    border: none;
    border-radius: 8px;
    
    transition: background 5s ease-out;
    
    &:hover {
       background: linear-gradient(90deg, #0B934A 2.61%, #026E47 112.6%); 
    }
    
    &:active {
        background: linear-gradient(90deg, #076432 2.61%, #013C27 112.6%);
    }
`;

export const MainTitle = () => {
    return (
        <Container>
            <Header>
                <Text color='#0EC261' text='Need for drive' size='30px' margin='0' weight='bold'/>
                <HeaderPlace>
                    <HeaderImg src={MapPoint}/>
                    <Text color='#999999' text='Ульяновск' margin='0' size='14px' weight='normal'/>
                </HeaderPlace>
            </Header>
            <Main>
                <MainContainer>
                    <Text color='#121212' text='Каршеринг' size='70px' margin='0'  weight='bold'/>
                    <Text color='#0EC261' text='Need for drive' size='70px' margin='34px' weight='bold'/>
                    <Text color='#999999' text='Поминутная аренда авто твоего города' size='26px' margin='0' weight='300'/>
                </MainContainer>
                <Button>Забронировать</Button>
            </Main>
            <Footer>
                <Text color='#999999' text='© 2016-2019 «Need for drive»' size='13px' margin='0' weight='normal'/>
                <Text color='#121212' text='8 (495) 234-22-44' size='13px' margin='0' weight='normal'/>
            </Footer>
        </Container>
    );
};
