import styled from "styled-components";
import React from 'react';

import CloseBtn from '../../../img/close_btn.svg';
import CloseBtnHover from '../../../img/close_btn_hover.svg';
import Inst from '../../../img/Instagram_white.svg';
import Fb from '../../../img/Facebook_white.svg';
import Tg from '../../../img/Telegram_white.svg';
import InstHover from '../../../img/Instagram_hover.svg';
import FbHover from '../../../img/Facebook_hover.svg';
import TgHover from '../../../img/Telegram_hover.svg';

const ModalMenuContainer = styled.div`
    display: flex;
    z-index: 9999;
    position: absolute;
    height: 100%;
    width: 100%;
    
    transition: left 5s ease;
`;

const Left = styled.div`
    width: 4%;
    height: 100%;
    background-color: black;
    display: flex;
    flex-direction: column;
    justify-content: center;
     
    @media(max-width: 819px) {
        width: 6%; 
    }
`;

const Middle = styled.div`
    width: 48%;
    background-color: black;
    display: flex;
    flex-direction: column;
    justify-content: center;
    padding-left: 64px;
    
    @media(max-width: 1084px) {
        width: 58%; 
    }
     @media(max-width: 819px) {
        width: 94%; 
    }
    
`;

const Right = styled.div`
    width: 48%;
    background: black;
    opacity: .7;
    
    @media(max-width: 1084px) {
        width: 38%; 
    }
     @media(max-width: 819px) {
        display: none;
    }
`;

const Close = styled.div`
    cursor: pointer;
    position: absolute;
    width: 32px;
    height: 32px;
    top: 32px;
    left: 16px;
    background-image: url(${CloseBtn});
    
    transition: background-image .5s ease;

    &:hover {
        background-image: url(${CloseBtnHover});
    }
`;

const Main = styled.div``;

const Ul = styled.div`
    margin-bottom: 38px;
`;

const MenuLi = (props) => {
    const Li = styled.div`
        cursor: pointer;
        color: #FFFFFF;
        font-weight: 500;
        font-size: 32px;
        
        transition: color .5s ease;
        
        &:hover {
            color: #0EC261;
        }
    `;
    return <Li>{props.text}</Li>
}

const Social = styled.div`
    display: flex;
`;

const SocialImg = (props) => {
    const Img = styled.div`
        cursor: pointer; 
        margin-right: 24px;
        height: 32px;
        width: 32px;
        background-image: url(${props.img});
        
        transition: background-image .5s ease;
        
        &:hover {
            background-image: url(${props.hover});
        }
    `;
    return <Img/>
}

export const MainModalMenu = ({setOpenItem}) => {

    return <React.Fragment>
        <ModalMenuContainer>
            <Left>
                <Close onClick={() => setOpenItem(false)}/>
            </Left>
            <Middle>
                <Main>
                    <Ul>
                        <MenuLi text={`ПАРКОВКА`}/>
                        <MenuLi text={`СТРАХОВКА`}/>
                        <MenuLi text={`БЕНЗИН`}/>
                        <MenuLi text={`ОБСЛУЖИВАНИЕ`}/>
                    </Ul>
                    <Social>
                        <SocialImg img={Tg} hover={TgHover}/>
                        <SocialImg img={Fb} hover={FbHover}/>
                        <SocialImg img={Inst} hover={InstHover}/>
                    </Social>
                </Main>
            </Middle>
            <Right/>
        </ModalMenuContainer>
    </React.Fragment>

};
