import styled from "styled-components";
import React from 'react';
import CloseBtn from "../../../img/close_btn.svg";
import CloseBtnHover from "../../../img/close_btn_hover.svg";
import Tg from "../../../img/Telegram_white.svg";
import TgHover from "../../../img/Telegram_hover.svg";
import Fb from "../../../img/Facebook_white.svg";
import FbHover from "../../../img/Facebook_hover.svg";
import Inst from "../../../img/Instagram_white.svg";
import InstHover from "../../../img/Instagram_hover.svg";

const MobileContainer = styled.div`
    z-index: 9999;
    position: absolute;
    height: 100%;
    width: 100%;
    background: #121212;
    
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    padding: 28px;
    
`;

const Content = styled.div`
    display: flex;
    flex-direction: column;
`;

const Footer = styled.div`
    line-height: 1;
    font-weight: bold;
    text-align: left;
    font-size: 13px;
    color: #0EC261;
`;

const Close = styled.div`
    display: flex;
    cursor: pointer;
    width: 32px;
    height: 32px;
    margin-bottom: 28px;
    background-image: url(${CloseBtn});
    
    transition: background-image .5s ease;

    &:hover {
        background-image: url(${CloseBtnHover});
    }
`;

const Main = styled.div``;

const Ul = styled.div`
        margin-bottom: 38px;
    `;
const MenuLi = (props) => {
    const Li = styled.div`
            cursor: pointer;
            color: #FFFFFF;
            font-weight: 500;
            font-size: 22px;
            
            transition: color .5s ease;
            
            &:hover {
                color: #0EC261;
            }
        `;

    return <Li>{props.text}</Li>
}

const Social = styled.div`
        display: flex;
    `;
const SocialImg = (props) => {
    const Img = styled.div`
            cursor: pointer; 
            margin-right: 24px;
            height: 32px;
            width: 32px;
            background-image: url(${props.img});
            
            transition: background-image .5s ease;
            
            &:hover {
                background-image: url(${props.hover});
            }
        `;

    return <Img/>
}

export const MainModalMenuMobile = ({setOpenItem}) => {
    return (
        <MobileContainer>
            <Content>
                <Close onClick={() => setOpenItem(false)}/>
                <Main>
                    <Ul>
                        <MenuLi text={`ПАРКОВКА`}/>
                        <MenuLi text={`СТРАХОВКА`}/>
                        <MenuLi text={`БЕНЗИН`}/>
                        <MenuLi text={`ОБСЛУЖИВАНИЕ`}/>
                    </Ul>
                    <Social>
                        <SocialImg img={Tg} hover={TgHover}/>
                        <SocialImg img={Fb} hover={FbHover}/>
                        <SocialImg img={Inst} hover={InstHover}/>
                    </Social>
                </Main>
            </Content>
            <Footer>
                Eng
            </Footer>
        </MobileContainer>
    );
}
