import React from 'react';
import styled from "styled-components";

import MenuBurgerImg from '../../../img/menu_burger_mobile.svg';
import MapPoint from '../../../img/map-point.svg';

export const Text = (props) => {
    const Text = styled.p`
        line-height: 1;
        font-weight: ${props.weight};
        margin-bottom: ${props.margin};
        font-size: ${props.size};
        color: ${props.color};
    `;
    return <Text>{props.text}</Text>
};

const Container = styled.div`
    height: 100%;
    width: 100%;
    display: flex;
    flex-direction: column;
    justify-content: space-between;                     
`;

const Header = styled.div`
    margin: 16px 16px 0 16px;
`;

const Main = styled.div`
    padding: 0 16px 0 16px;
`;

const Footer = styled.div`
    background: #151B1F;
    text-align: right;
    padding: 16px;
`;

const FirstHeaderRow = styled.div`
    display: flex;
    justify-content: space-between;
    margin-bottom: 10px;
`;
const SecondHeaderRow = styled.div`
    display: flex;
    justify-content: flex-end;
    align-items: center;
`;

const HeaderImg = styled.img`
    margin-right: 8px;
`;

const Button = styled.button`
    cursor: pointer;
    line-height: 1;
    font-weight: 500;
    font-size: 18px;
    color: #FFFFFF;
    width: 100%;
    
    padding: 15px 60px 15px 60px;
    
    background: linear-gradient(90deg, #0EC261 2.61%, #039F67 112.6%);
    border: none;
    border-radius: 8px;
    
    transition: background 5s ease-out;
    
    &:active {
        background: linear-gradient(90deg, #076432 2.61%, #013C27 112.6%);
    }
`;

export const MainMobile = ({setOpenItem}) => {
    return (
        <Container>
            <Header>
                <FirstHeaderRow>
                    <img src={MenuBurgerImg} onClick={() => setOpenItem(true)} alt='Меню'/>
                    <Text text='Need for drive' weight='bold' margin='0' color='#0EC261' size='30px'/>
                </FirstHeaderRow>
                <SecondHeaderRow>
                    <HeaderImg src={MapPoint}/>
                    <Text text='Ульяновск' weight='normal' margin='0' color='#999999' size='14px'/>
                </SecondHeaderRow>
            </Header>
            <Main>
                <Text margin='6px' color='#121212' text='Каршеринг' size='32px' weight='bold'/>
                <Text margin='16px' color='#0EC261' text='Need for drive' size='32px' weight='bold'/>
                <Text margin='32px' color='#999999' text='Поминутная аренда авто твоего город' size='18px' weight='300'/>
                <Button>Забронировать</Button>
            </Main>
            <Footer>
                <Text text='8 (495) 234-22-44' color='#0EC261' weight='normal' margin='8px' size='13px'/>
                <Text text='© 2016-2019 «Need for drive»' color='#EEEEEE' weight='normal' margin='0' size='13px'/>
            </Footer>
        </Container>
    )
};
