import React from 'react';
import styled, {createGlobalStyle} from "styled-components";
import Carousel from 'react-bootstrap/Carousel'

import slide1 from '../../../img/slider1.png';
import slide2 from '../../../img/slider2.png';
import slide3 from '../../../img/slider3.png';
import slide4 from '../../../img/slider4.png';

const Style = createGlobalStyle`
    .carousel-inner {
        height: 100%;
    }
    
    .carousel-indicators {
        margin-bottom: 32px;
    }
    .carousel-indicators li:hover {
        background-color: #0EC261;
    }
    .carousel-indicators .active {
        background-color: #0EC261;
        opacity: .9;
    }
   
    .carousel-control-next, .carousel-control-prev {
        opacity: .5;
        transition: none;
        width: 10%;
    }
    .carousel-control-next:hover, .carousel-control-prev:hover {
        opacity: 0.2;
        background-color: #0EC261;
    } 
`;

const Slider = (props) => {
    const Slide = styled.div`
        height: 100%;
        display: flex;
        flex-direction: column;
        justify-content: center;
        background: url(${props.img});
        background-size: cover;
    `;

    const SlideContent = styled.div`
        padding: 20%;
        @media(max-width: 1024px) {
            padding: 10%; 
        }
    `;

    const Title = styled.p`
        font-weight: 500;
        font-size: 40px;
        color: #FFFFFF;
        margin-bottom: 8px;
        
        @media(max-width: 1024px) {
             font-size: 30px;
        }
    `;

    const Second = styled.p`
        font-weight: 300;
        font-size: 24px;
        color: #FFFFFF;
        margin-bottom: 32px;
    `;

    const Button = styled.button`
        color: #FFFFFF;
        line-height: 1;
        font-weight: 500;
        font-size: 18px;
        
        padding: 15px 60px 15px 60px;
        
        border: none;
        background: linear-gradient(${props.gradient});
        border-radius: 8px;
        
        &:hover {
           background: linear-gradient(${props.hoverGradient}); 
        }
        
        &:active {
            background: linear-gradient(${props.clickGradient});
    }
    `;

    return <React.Fragment>
        <Slide>
            <SlideContent>
                <Title>
                    {props.title}
                </Title>
                <Second>
                    {props.second}
                </Second>
                <Button>
                    Подробнее
                </Button>
            </SlideContent>
        </Slide>
    </React.Fragment>
}

export const MainSlider = () => (
    <React.Fragment>
        <Style/>
        <Carousel className="h-100">
            <Carousel.Item className="h-100">
                <Slider img={slide1}
                        title="Бесплатная парковка"
                        second="Оставляйте машину на платных городских парковках и разрешенных местах, не нарушая ПДД, а также в аэропортах."
                        hrer="#"
                        gradient={`90deg, #13493F 0%, #0C7B1B 100%`}
                        hoverGradient={`90deg, #0E352E 0%, #085612 100%`}
                        clickGradient={`90deg, #071B17 0%, #074710 100%`}
                />
            </Carousel.Item>
            <Carousel.Item className="h-100">
                <Slider img={slide2}
                        title="Страховка"
                        second="Полная страховка страховка автомобиля."
                        hrer="#"
                        gradient={`90deg, #132949 0%, #0C7B67 100%`}
                        hoverGradient={`90deg, #0D1C33 0%, #074F42 100%`}
                        clickGradient={`90deg, #070F1B 0%, #05332B 100%`}
                />
            </Carousel.Item>
            <Carousel.Item className="h-100">
                <Slider img={slide3}
                        title="Бензин"
                        second="Полный бак на любой заправке города за наш счёт."
                        hrer="#"
                        gradient={`90deg, #493013 0%, #7B0C3B 100%`}
                        hoverGradient={`90deg, #2D1D0B 0%, #5F082C 100%`}
                        clickGradient={`90deg, #181006 0%, #460722 100%`}
                />
            </Carousel.Item>
            <Carousel.Item className="h-100">
                <Slider img={slide4}
                        title="Обслуживание"
                        second="Автомобиль проходит еженедельное ТО."
                        hrer="#"
                        gradient={`90deg, #281349 0%, #720C7B 100%`}
                        hoverGradient={`90deg, #190C2F 0%, #520858 100%`}
                        clickGradient={`90deg, #0F071D 0%, #38063D 100%`}
                />
            </Carousel.Item>
        </Carousel>
    </React.Fragment>
);
