import React from 'react';
import styled from "styled-components";

import { MainMenu } from "./Menu/MainMenu";
import { MainTitle } from "./Title/MainTitle";
import { MainSlider } from "./Slider/MainSlider";
import { MainMobile } from "./Mobile/MainMobile";
import { MainModalMenu } from "./Menu/MainModalMenu";
import { MainModalMenuMobile } from "./Mobile/MainModalMenuMobile";

import { useModalMenu } from "../../Hooks/useModalMenu";

const Container = styled.div`
    display: flex;
    height: 100vh;
    
    @media(max-width: 560px) {
        display: none;
    }
`;

const ContainerMobile = styled.div`
    height: 100vh;
    display: none;
    
    @media(max-width: 560px) {
        display: flex;
    }
`;

const MainMenuContainer = styled.section`
    width: 4%;
    height: 100%;
    background: #151B1F;;
     
    @media(max-width: 819px) {
        width: 6%; 
    }
`;

const MainTitleContainer = styled.section`
    width: 48%;
    height: 100%;
    
    @media(max-width: 1084px) {
        width: 58%; 
    }
    @media(max-width: 819px) {
        width: 94%; 
    }
`;

const MainSliderContainer = styled.section`
    width: 48%;
    height: 100%;
    
    @media(max-width: 1084px) {
        text-align: center;
        width: 38%; 
    }
    @media(max-width: 819px) {
        display: none;
    }
`;

export const MainPage = () => {

    const modalMenu = useModalMenu();

    return (
        <React.Fragment>
            <Container>
                { modalMenu.openItem ? <MainModalMenu {...modalMenu}/> : ''}
                <MainMenuContainer>
                    <MainMenu {...modalMenu}/>
                </MainMenuContainer>
                <MainTitleContainer>
                    <MainTitle/>
                </MainTitleContainer>
                <MainSliderContainer>
                    <MainSlider/>
                </MainSliderContainer>
            </Container>
            <ContainerMobile>
                <MainMobile {...modalMenu}/>
                { modalMenu.openItem ? <MainModalMenuMobile {...modalMenu}/> : ''}
            </ContainerMobile>
        </React.Fragment>
    );
}
